/* === BASE HEADING === */

h1 {
  position: relative;
  padding: 0;
  margin: 0;
  font-family: "Raleway", sans-serif;
  font-weight: 300;
  font-size: 40px;
  color: #080808;
  -webkit-transition: all 0.4s ease 0s;
  -o-transition: all 0.4s ease 0s;
  transition: all 0.4s ease 0s;
}

h1 span {
  display: block;
  font-size: 0.5em;
  line-height: 1.3;
}
h1 em {
  font-style: normal;
  font-weight: 600;
}
/* Image Container Style */
header .imgContainer h1 {
  text-align: center;
  font-size: 50px;
  text-transform: uppercase;
  color: #222;
  letter-spacing: 1px;
  font-family: "Playfair Display", serif;
  font-weight: 400;
}
header .imgContainer h1 span {
  margin-top: 5px;
  font-size: 15px;
  color: #444;
  word-spacing: 1px;
  font-weight: normal;
  letter-spacing: 2px;
  text-transform: uppercase;
  font-family: "Raleway", sans-serif;
  font-weight: 500;
  display: grid;
  grid-template-columns: 1fr max-content 1fr;
  grid-template-rows: 27px 0;
  grid-gap: 20px;
  align-items: center;
}

header .imgContainer h1 span:after,
header .imgContainer h1 span:before {
  content: " ";
  display: block;
  border-bottom: 1px solid #ccc;
  border-top: 1px solid #ccc;
  height: 5px;
  background-color: #f8f8f8;
}

/* Todo App CSS */
body {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size: cover;
  font-family: "Raleway", sans-serif;
}

.todoApp {
  display: grid;
  grid-template-columns: minmax(100px, 1fr) minmax(500px, 4fr);
  width: 100%;
  height: 80vh;
}
.sideBar {
  background-color: antiquewhite;
  padding: 20px;
}
.sideBar h5 {
  font-size: 1.5rem;
  font-family: "Playfair Display", serif;
  line-height: 1.5;
  letter-spacing: 1.5px;
  margin: 1em 0;
}
.main {
  background-color: aquamarine;
}
.side_menu {
  display: flex;
  justify-content: start;
  align-items: center;
  /* font-family: "Playfair Display", serif; */
  font-weight: 500;
  margin-bottom: 0.75em;
}
.side_menu:hover {
  background-color: #dfdfdf;
  cursor: pointer;
}
.side_menu.active {
  background-color: #dfdfdf;
}
.side_menu img {
  height: 100%;
  max-height: 50px;
  width: 100%;
  max-width: 50px;
}
.default_unstyle_btn {
  border: none;
  margin: 0;
  padding: 0;
  width: auto;
  overflow: visible;

  background: transparent;

  /* inherit font & color from ancestor */
  color: inherit;
  font: inherit;

  /* Normalize \`line-height\`. Cannot be changed from \`normal\` in Firefox 4+. */
  line-height: normal;

  /* Corrects font smoothing for webkit */
  -webkit-font-smoothing: inherit;
  -moz-osx-font-smoothing: inherit;

  /* Corrects inability to style clickable \`input\` types in iOS */
  -webkit-appearance: none;
}

/* Remove excess padding and border in Firefox 4+ */
&::-moz-focus-inner {
  border: 0;
  padding: 0;
}
.add_Task_btn img {
  height: 100%;
  max-height: 50px;
  width: 100%;
  max-width: 50px;
}
.add_Task_btn {
  display: flex;
  justify-content: center;
  align-items: center;
  /* font-family: "Playfair Display", serif; */
  font-weight: 500;
  margin-bottom: 0.75em;
  cursor: pointer;
  width: 100%;
  margin-top: 4px;
  background-color: rgb(64, 135, 242);
  color: #f8f8f8;
}
.add_Task_btn:hover {
  background-color: #dfdfdf;
  color: #080808;
}
/* Checkbox  CSS*/
.task_checkbox {
  --borderColor: #48c;
  --borderWidth: 0.125em;
}

.task_checkbox label {
  display: block;
  max-width: 100%;
  margin: 0 auto;
}

.task_checkbox input[type="checkbox"] {
  -webkit-appearance: none;
  appearance: none;
  vertical-align: middle;
  background: #fff;
  font-size: 1.8em;
  border-radius: 0.125em;
  display: inline-block;
  border: var(--borderWidth) solid var(--borderColor);
  width: 1em;
  height: 1em;
  position: relative;
}
.task_checkbox input[type="checkbox"]:before,
.task_checkbox input[type="checkbox"]:after {
  content: "";
  position: absolute;
  background: var(--borderColor);
  width: calc(var(--borderWidth) * 3);
  height: var(--borderWidth);
  top: 50%;
  left: 10%;
  transform-origin: left center;
}
.task_checkbox input[type="checkbox"]:before {
  transform: rotate(45deg)
    translate(calc(var(--borderWidth) / -2), calc(var(--borderWidth) / -2))
    scaleX(0);
  transition: transform 200ms ease-in 200ms;
}
.task_checkbox input[type="checkbox"]:after {
  width: calc(var(--borderWidth) * 5);
  transform: rotate(-45deg) translateY(calc(var(--borderWidth) * 2)) scaleX(0);
  transform-origin: left center;
  transition: transform 200ms ease-in;
}
.task_checkbox input[type="checkbox"]:checked:before {
  transform: rotate(45deg)
    translate(calc(var(--borderWidth) / -2), calc(var(--borderWidth) / -2))
    scaleX(1);
  transition: transform 200ms ease-in;
}
.task_checkbox input[type="checkbox"]:checked:after {
  width: calc(var(--borderWidth) * 5);
  transform: rotate(-45deg) translateY(calc(var(--borderWidth) * 2)) scaleX(1);
  transition: transform 200ms ease-out 200ms;
}
.task_checkbox input[type="checkbox"]:focus {
  outline: calc(var(--borderWidth) / 2) dotted rgba(0, 0, 0, 0.25);
}
.single_project {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.5rem;
}
.left_project-area,
.right_project_area {
  display: flex;
  justify-content: center;
  align-items: center;
}
.title_area {
  margin-left: 1em;
  /* margin: 1em auto; */
}
.title_area .title,
.title_area p {
  margin: 4px 0;
}
.title_area .title {
  font-size: 1rem;
}
.task_checkbox {
  display: flex;
  justify-content: center;
  align-items: center;
}
.edit_task,
.delete_task {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  /* border-width: 10%; */
}
.edit_task:hover,
.delete_task:hover {
  background-color: #6260603c;
}
.edit_task:active,
.delete_task:active {
  background-color: #1d1d1d3c;
}
.edit_task img,
.delete_task img {
  height: 100%;
  max-height: 50px;
  width: 100%;
  max-width: 40px;
  margin: auto;
}
.right_project_area * {
  margin-right: 10px;
}
.right_project_area {
  display: grid;
  max-width: 280px;
  grid-template-columns: 2fr 3fr 1fr 1fr;
}
.right_project_area .detail_btn {
  background-color: rgb(212, 155, 155);
  border: none;
  border-radius: 10%;
  padding: 0.75em 1em;
  font-family: "Raleway", sans-serif;
  font-weight: 500;
}
.right_project_area .detail_btn:hover {
  cursor: pointer;
}
.right_project_area .detail_btn:active {
  transform: scale(1.1, 1.025);
}
.list_all_projects {
  padding: 10px;
  overflow: hidden;
}
.Project_Title {
  margin: 10px auto;
  display: flex;
  justify-content: left;
  /* margin-left: 15px; */
  border-bottom: #080808 solid 1px;
  padding: 0 20px;
  width: 100%;
  font-family: "Playfair Display", serif;
  font-weight: 500;
  font-size: 1.75rem;
  letter-spacing: 1px;
}

/* Priority */
.high {
  border: 1px solid rgb(196, 196, 196);
  border-left: 4px solid rgb(205, 68, 68);
}
.medium {
  border: 1px solid rgb(196, 196, 196);
  border-left: 4px solid rgb(240, 216, 36);
}
.low {
  border: 1px solid rgb(196, 196, 196);
  border-left: 4px solid rgb(86, 205, 68);
}
.noPriority {
  border: 1px solid rgb(196, 196, 196);
  border-left: 4px solid rgb(179, 179, 179);
}
/* dialog {
  z-index: 10;
} */
/* dialog {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 50%;
  height: 50%; */
/* Add any other styles you want for the dialog box
} */
/* 
dialog::backdrop {
  background: rgba(0, 0, 0, 0.5); 
} 
*/
.modalArea {
  position: fixed; /* Stay in place */
  z-index: 1; /* Sit on top */
  padding-top: 100px; /* Location of the box */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0, 0, 0); /* Fallback color */
  background-color: rgba(0, 0, 0, 0.4); /* Black w/ opacity */
}
.modal_head {
  display: grid;
  width: 100%;
  grid-template-columns: 10fr 1fr;
  position: relative;
}
.modal_head h5 {
  text-align: center;
  font-size: 1.5rem;
  /* line-height: 10px; */
  margin: auto;
}
.modal_Content {
  background-color: #fefefe;
  margin: auto;
  padding: 20px;
  border: 1px solid #888;
  width: 70%;
}
.close {
  color: #aaaaaa;
  justify-self: end;
  /* position: absolute;
  right: 0;
  top: 0; */
  font-size: 3em;
  font-weight: bold;
  cursor: pointer;
}
.openItem {
  display: block;
}
.closeItem {
  display: none;
}

.animateClose {
  animation: scaleBtn 0.5s ease-in-out infinite;
}
#detailModal {
  width: 100%;
}
#detailModal .detail_area {
  display: grid;
  grid-template-columns: minmax(100px, 1fr) minmax(200px, 4fr);
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
#detailModal .detail_area h2 {
  text-transform: capitalize;
}
@keyframes scaleBtn {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.5);
  }
  100% {
    transform: scale(1);
  }
}
