\n  font-size: 1.5rem;\r\n  font-family: \"Playfair Display\", serif;\r\n  line-height: 1.5;\r\n  letter-spacing: 1.5px;\r\n  margin: 1em 0;\r\n}\r\n.main {\r\n  background-color: aquamarine;\r\n}\r\n.side_menu {\r\n  display: flex;\r\n  justify-content: start;\r\n  align-items: center;\r\n  /* font-family: \"Playfair Display\", serif; */\r\n  font-weight: 500;\r\n  margin-bottom: 0.75em;\r\n}\r\n.side_menu:hover {\r\n  background-color: #dfdfdf;\r\n  cursor: pointer;\r\n}\r\n.side_menu.active {\r\n  background-color: #dfdfdf;\r\n}\r\n.side_menu img {\r\n  height: 100%;\r\n  max-height: 50px;\r\n  width: 100%;\r\n  max-width: 50px;\r\n}\r\n.default_unstyle_btn {\r\n  border: none;\r\n  margin: 0;\r\n  padding: 0;\r\n  width: auto;\r\n  overflow: visible;\r\n\r\n  background: transparent;\r\n\r\n  /* inherit font & color from ancestor */\r\n  color: inherit;\r\n  font: inherit;\r\n\r\n  /* Normalize `line-height`. Cannot be changed from `normal` in Firefox 4+. */\r\n  line-height: normal;\r\n\r\n  /* Corrects font smoothing for webkit */\r\n  -webkit-font-smoothing: inherit;\r\n  -moz-osx-font-smoothing: inherit;\r\n\r\n  /* Corrects inability to style clickable `input` types in iOS */\r\n  -webkit-appearance: none;\r\n}\r\n\r\n/* Remove excess padding and border in Firefox 4+ */\r\n&::-moz-focus-inner {\r\n  border: 0;\r\n  padding: 0;\r\n}\r\n.add_Task_btn img {\r\n  height: 100%;\r\n  max-height: 50px;\r\n  width: 100%;\r\n  max-width: 50px;\r\n}\r\n.add_Task_btn {\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n  /* font-family: \"Playfair Display\", serif; */\r\n  font-weight: 500;\r\n  margin-bottom: 0.75em;\r\n  cursor: pointer;\r\n  width: 100%;\r\n  margin-top: 4px;\r\n  background-color: rgb(64, 135, 242);\r\n  color: #f8f8f8;\r\n}\r\n.add_Task_btn:hover {\r\n  background-color: #dfdfdf;\r\n  color: #080808;\r\n}\r\n/* Checkbox  CSS*/\r\n.task_checkbox {\r\n  --borderColor: #48c;\r\n  --borderWidth: 0.125em;\r\n}\r\n\r\n.task_checkbox label {\r\n  display: block;\r\n  max-width: 100%;\r\n  margin: 0 auto;\r\n}\r\n\r\n.task_checkbox input[type=\"checkbox\"] {\r\n  -webkit-appearance: none;\r\n  appearance: none;\r\n  vertical-align: middle;\r\n  background: #fff;\r\n  font-size: 1.8em;\r\n  border-radius: 0.125em;\r\n  display: inline-block;\r\n  border: var(--borderWidth) solid var(--borderColor);\r\n  width: 1em;\r\n  height: 1em;\r\n  position: relative;\r\n}\r\n.task_checkbox input[type=\"checkbox\"]:before,\r\n.task_checkbox input[type=\"checkbox\"]:after {\r\n  content: \"\";\r\n  position: absolute;\r\n  background: var(--borderColor);\r\n  width: calc(var(--borderWidth) * 3);\r\n  height: var(--borderWidth);\r\n  top: 50%;\r\n  left: 10%;\r\n  transform-origin: left center;\r\n}\r\n.task_checkbox input[type=\"checkbox\"]:before {\r\n  transform: rotate(45deg)\r\n    translate(calc(var(--borderWidth) / -2), calc(var(--borderWidth) / -2))\r\n    scaleX(0);\r\n  transition: transform 200ms ease-in 200ms;\r\n}\r\n.task_checkbox input[type=\"checkbox\"]:after {\r\n  width: calc(var(--borderWidth) * 5);\r\n  transform: rotate(-45deg) translateY(calc(var(--borderWidth) * 2)) scaleX(0);\r\n  transform-origin: left center;\r\n  transition: transform 200ms ease-in;\r\n}\r\n.task_checkbox input[type=\"checkbox\"]:checked:before {\r\n  transform: rotate(45deg)\r\n    translate(calc(var(--borderWidth) / -2), calc(var(--borderWidth) / -2))\r\n    scaleX(1);\r\n  transition: transform 200ms ease-in;\r\n}\r\n.task_checkbox input[type=\"checkbox\"]:checked:after {\r\n  width: calc(var(--borderWidth) * 5);\r\n  transform: rotate(-45deg) translateY(calc(var(--borderWidth) * 2)) scaleX(1);\r\n  transition: transform 200ms ease-out 200ms;\r\n}\r\n.task_checkbox input[type=\"checkbox\"]:focus {\r\n  outline: calc(var(--borderWidth) / 2) dotted rgba(0, 0, 0, 0.25);\r\n}\r\n.single_project {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n  margin-bottom: 0.5rem;\r\n}\r\n.left_project-area,\r\n.right_project_area {\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n}\r\n.title_area {\r\n  margin-left: 1em;\r\n  /* margin: 1em auto; */\r\n}\r\n.title_area .title,\r\n.title_area p {\r\n  margin: 4px 0;\r\n}\r\n.title_area .title {\r\n  font-size: 1rem;\r\n}\r\n.task_checkbox {\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n}\r\n.edit_task,\r\n.delete_task {\r\n  cursor: pointer;\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n  /* border-width: 10%; */\r\n}\r\n.edit_task:hover,\r\n.delete_task:hover {\r\n  background-color: #6260603c;\r\n}\r\n.edit_task:active,\r\n.delete_task:active {\r\n  background-color: #1d1d1d3c;\r\n}\r\n.edit_task img,\r\n.delete_task img {\r\n  height: 100%;\r\n  max-height: 50px;\r\n  width: 100%;\r\n  max-width: 40px;\r\n  margin: auto;\r\n}\r\n.right_project_area * {\r\n  margin-right: 10px;\r\n}\r\n.right_project_area {\r\n  display: grid;\r\n  max-width: 280px;\r\n  grid-template-columns: 2fr 3fr 1fr 1fr;\r\n}\r\n.right_project_area .detail_btn {\r\n  background-color: rgb(212, 155, 155);\r\n  border: none;\r\n  border-radius: 10%;\r\n  padding: 0.75em 1em;\r\n  font-family: \"Raleway\", sans-serif;\r\n  font-weight: 500;\r\n}\r\n.right_project_area .detail_btn:hover {\r\n  cursor: pointer;\r\n}\r\n.right_project_area .detail_btn:active {\r\n  transform: scale(1.1, 1.025);\r\n}\r\n.list_all_projects {\r\n  padding: 10px;\r\n  overflow: hidden;\r\n}\r\n.Project_Title {\r\n  margin: 10px auto;\r\n  display: flex;\r\n  justify-content: left;\r\n  /* margin-left: 15px; */\r\n  border-bottom: #080808 solid 1px;\r\n  padding: 0 20px;\r\n  width: 100%;\r\n  font-family: \"Playfair Display\", serif;\r\n  font-weight: 500;\r\n  font-size: 1.75rem;\r\n  letter-spacing: 1px;\r\n}\r\n\r\n/* Priority */\r\n.high {\r\n  border: 1px solid rgb(196, 196, 196);\r\n  border-left: 4px solid rgb(205, 68, 68);\r\n}\r\n.medium {\r\n  border: 1px solid rgb(196, 196, 196);\r\n  border-left: 4px solid rgb(240, 216, 36);\r\n}\r\n.low {\r\n  border: 1px solid rgb(196, 196, 196);\r\n  border-left: 4px solid rgb(86, 205, 68);\r\n}\r\n.noPriority {\r\n  border: 1px solid rgb(196, 196, 196);\r\n  border-left: 4px solid rgb(179, 179, 179);\r\n}\r\n/* dialog {\r\n  z-index: 10;\r\n} */\r\n/* dialog {\r\n  position: fixed;\r\n  top: 50%;\r\n  left: 50%;\r\n  transform: translate(-50%, -50%);\r\n  width: 50%;\r\n  height: 50%; */\r\n/* Add any other styles you want for the dialog box\r\n} */\r\n/* \r\ndialog::backdrop {\r\n  background: rgba(0, 0, 0, 0.5); \r\n} \r\n*/\r\n.modalArea {\r\n  position: fixed; /* Stay in place */\r\n  z-index: 1; /* Sit on top */\r\n  padding-top: 100px; /* Location of the box */\r\n  left: 0;\r\n  top: 0;\r\n  width: 100%; /* Full width */\r\n  height: 100%; /* Full height */\r\n  overflow: auto; /* Enable scroll if needed */\r\n  background-color: rgb(0, 0, 0); /* Fallback color */\r\n  background-color: rgba(0, 0, 0, 0.4); /* Black w/ opacity */\r\n}\r\n.modal_head {\r\n  display: grid;\r\n  width: 100%;\r\n  grid-template-columns: 10fr 1fr;\r\n  position: relative;\r\n}\r\n.modal_head h5 {\r\n  text-align: center;\r\n  font-size: 1.5rem;\r\n  /* line-height: 10px; */\r\n  margin: auto;\r\n}\r\n.modal_Content {\r\n  background-color: #fefefe;\r\n  margin: auto;\r\n  padding: 20px;\r\n  border: 1px solid #888;\r\n  width: 70%;\r\n}\r\n.close {\r\n  color: #aaaaaa;\r\n  justify-self: end;\r\n  /* position: absolute;\r\n  right: 0;\r\n  top: 0; */\r\n  font-size: 3em;\r\n  font-weight: bold;\r\n  cursor: pointer;\r\n}\r\n.openItem {\r\n  display: block;\r\n}\r\n.closeItem {\r\n  display: none;\r\n}\r\n\r\n.animateClose {\r\n  animation: scaleBtn 0.5s ease-in-out infinite;\r\n}\r\n#detailModal {\r\n  width: 100%;\r\n}\r\n#detailModal .detail_area {\r\n  display: grid;\r\n  grid-template-columns: minmax(100px, 1fr) minmax(200px, 4fr);\r\n  justify-content: space-between;\r\n  align-items: center;\r\n  width: 100%;\r\n}\r\n#detailModal .detail_area h2 {\r\n  text-transform: capitalize;\r\n}\r\n@keyframes scaleBtn {\r\n  0% {\r\n    transform: scale(1);\r\n  }\r\n  50% {\r\n    transform: scale(1.5);\r\n  }\r\n  100% {\r\n    transform: scale(1);\r\n  }\r\n}\r\n"],"sourceRoot":""}]);
